import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';


export default function BasicModal( {url, physical, color} ) {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const style = {
    position: 'absolute',
    top: '45%',
    left: '48%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: color,
    opacity: 0.94,
    border: '2px solid #aaa7a7',
    boxShadow: 24,
    p: 4,
    color: '#FFF'
  };
  
  return (
    <div>
      <img className="CrystalImage" src={url} width="170" height="170" onClick={handleOpen} />
      <Modal
        open={open}
        onClose={handleClose}
      >
        <Box sx={style}>
          <h3>
            Physical Characteristics
          </h3>
          <Typography variant="overline" sx={{ mt: 2 }}>
            <ul style={{listStyleType: "none"}}>
            <li><strong>Hardness:</strong> {physical.hardness}</li>
            <li><strong>Composition:</strong> {physical.composition}</li>
            <li><strong>Mineral family:</strong> {physical.family}</li>
            <li><strong>Luster:</strong> {physical.luster}</li>
            </ul>
          </Typography>
        </Box>
      </Modal>
    </div>
  );
}
