import React from "react";
import { Card, CardContent, Typography } from "@mui/material";
import GlobalMining from "./GlobalMining.js";
import ParticleComponent from './ParticleComponent';
import Modal from './Modal.js';


const CardLayout = ({ name, meta, healing, purpose, url, mining, physical, color}) => {

  const colors = ['#644536', '#3C3B40', '#047252', '#CE7D4B', '#1E3489', '#EDBFBE'];
  
  //this is the crystal card setup. it retrieves data through props
  return (
    <div className="App">
      <ParticleComponent theme=''/>
        <Card
        position="absolute"
          className="Card"
          raised="true"
          variant="outlined"
          sx={{ minWidth: 720}}
          style={{backgroundColor: "#000" }}
        >
         
          <CardContent position="absolute">

            <h4 className="title" style={{color: "#fff" }} >{name}</h4>
            <Typography style={{color: "#fff", opacity: "80%" }} variant="h5" mb="10px" color="text.secondary">
              {purpose}
            </Typography>
           <Modal url={url} physical={physical} hardness={physical.hardness} composition={physical.composition} family={physical.family} luster={physical.luster} color={color} />
            <Typography
              className="paragraph"
              paragraph="true"
              variant="overline"
              align="left"
            >
              {healing}
            </Typography>
            <div className="global-mining-container">
              <GlobalMining mining={mining} color={color} />
            </div>
            <div>
              <div className="table" raised="true" variant="outlined">
                <tr>
                  <td style={{color: color, fontWeight: "bold" }}>Zodiac:</td>
                  <td>
                    <Typography variant="subtitle12">{meta.sign}</Typography>
                  </td>
                </tr>
                <tr>
                  <td style={{color: color, fontWeight: "bold" }}>Planet:</td>
                  <td>
                    <Typography variant="subtitle12">{meta.planet}</Typography>
                  </td>
                </tr>
                <tr>
                  <td style={{color: color, fontWeight: "bold" }}>Element:</td>
                  <td>
                    <Typography variant="subtitle12">{meta.element}</Typography>
                  </td>
                </tr>
                <tr>
                  <td style={{color: color, fontWeight: "bold" }}>Chakra:</td>
                  <td>
                    <Typography variant="subtitle12">{meta.chakra}</Typography>
                  </td>
                </tr>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

  );
};

export default CardLayout;
